@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
.app{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:40px 0;font-family:'Lato', sans-serif;font-size:20px;line-height:26px}

.products__wrap{padding:20px;box-shadow:2px 2px 20px rgba(255,165,0,0.5)}

.search-bar{display:flex;flex-direction:column}.search-bar input[type='text']{padding:10px;margin-bottom:10px;font-family:'Lato', sans-serif;font-size:20px;line-height:26px}.search-bar input[type="checkbox"]{margin-bottom:20px}

.product-table{width:100%}.product-table__th{border-bottom:1px solid #ddd}

.product-category{background-color:#e0ffd9;border-bottom:1px solid #ddd}

.product-row__td{border-bottom:1px solid #ddd}

